import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const vitaminaK = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina K"
                meta="Toda la información que necesitas sobre la vitamina K. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina K?"
                description="Toda la información que necesitas sobre la vitamina K. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina K?"
                lang="es"
            />

            <h1>Vitamina K</h1>
            <p></p>
                <PostIndex2 linksData={[
                        {"url": "#funciones", "text": "Funciones"},
                        {"url": "#alimentos", "text": "Alimentos"},
                        {"url": "#deficiencia", "text": "Deficiencia"},
                ]}
                />
                <hr/>
            <p class="introductionText">La vitamina K es una vitamina liposoluble que se presenta en dos formas. El tipo principal se llama filoquinona, que se encuentra en las verduras de hoja verde como la col, la col rizada y las espinacas. El otro tipo, las menaquinonas, se encuentran en algunos alimentos de origen animal y en alimentos fermentados.</p>
            <hr/>
                <p>La vitamina K se encuentra en todo el cuerpo, incluyendo el h&iacute;gado, el cerebro, el coraz&oacute;n, el p&aacute;ncreas y los huesos. Se descompone muy r&aacute;pidamente y se excreta en la orina o las heces. Debido a esto, rara vez alcanza niveles t&oacute;xicos en el cuerpo, incluso con altas ingestas, como puede ocurrir a veces con otras vitaminas liposolubles.</p>
                <p></p>
            <h2 name={"funciones"}>Vitamina K funciones</h2>
            <p></p>
            <p>La vitamina K ayuda a producir varias prote&iacute;nas necesarias para la coagulaci&oacute;n de la sangre y la formaci&oacute;n de huesos. La protrombina es una prote&iacute;na dependiente de la vitamina K que est&aacute; directamente involucrada en la coagulaci&oacute;n de la sangre. La osteocalcina es otra prote&iacute;na que requiere vitamina K para producir tejido &oacute;seo sano.</p>
            <p></p>
            <p>La vitamina K es un nutriente esencial necesario para responder a las lesiones, ya que regula la coagulación normal de la sangre. Además, al ayudar a transportar el calcio por todo el cuerpo, la vitamina K también puede ser útil para la salud de los huesos: puede reducir la pérdida de hueso y disminuir el riesgo de fracturas. También puede ayudar a prevenir la calcificación de las arterias y otros tejidos blandos.</p>
            <p>La vitamina K juega un papel clave en la coagulaci&oacute;n de la sangre, previniendo el sangrado excesivo. A diferencia de muchas otras vitaminas, la vitamina K no se utiliza t&iacute;picamente como suplemento alimenticio.</p>
            <p></p>
            <p></p>
            <h2 name={"alimentos"}>Alimentos con vitamina k</h2>
            <p></p>
            <ul>
            <li>Filoquinona</li>
            <li>Verduras de hoja verde, incluyendo col y nabos, col rizada, espinacas, br&oacute;coli, coles de Bruselas, col, lechugas</li>
            <li>Aceite de soja y canola</li>
            <li>Aderezos para ensaladas hechos con aceite de soja o de canola</li>
            <li>Batidos de reemplazo de comidas fortificadas</li>
            <li>Menaquinones</li>
            <li>Natto (soja fermentada)</li>
            <li>Peque&ntilde;as cantidades en carne, queso, huevos</li>
            </ul>
            <p></p>
            <p></p>
            <h2 name={"deficiencia"}>Deficiencia de vitamina K</h2>
            <p></p>
            <p>La deficiencia de vitamina K en adultos es poco frecuente, pero puede ocurrir en personas que toman medicamentos que bloquean el metabolismo de la vitamina K, como los antibi&oacute;ticos, o en aquellas con afecciones que causan una mala absorci&oacute;n de alimentos y nutrientes. Tambi&eacute;n es posible que haya una deficiencia en los reci&eacute;n nacidos porque la vitamina K no atraviesa la placenta y la leche materna contiene una cantidad baja. La cantidad limitada de prote&iacute;nas de coagulaci&oacute;n de la sangre al nacer aumenta el riesgo de hemorragias en los beb&eacute;s si no se les administran suplementos de vitamina K. A continuaci&oacute;n se enumeran los signos m&aacute;s comunes de una deficiencia.</p>
            <p></p>
            <ul>
            <li>Un mayor tiempo de coagulaci&oacute;n de la sangre o un tiempo prolongado de protrombina (seg&uacute;n la medici&oacute;n realizada en el consultorio del m&eacute;dico)</li>
            <li>Sangrado</li>
            <li>Hemorragia</li>
            <li>Osteopenia u osteoporosis</li>
            </ul>
            <p></p>
            <div style={{height: `300px`}}>
            </div>
        </Layout>
    )
}

export default vitaminaK
